.host {
  @extend .flex-centered;
}

.target {
  @extend .flex-centered;
}

.title {
  font-family: var(--app-text-main-font);
  font-size: 16px;
  color: var(--app-color-gray-light);
}

.requiredTitle {
  font-family: var(--app-text-main-font);
  font-size: 16px;
  color: var(--app-color-red-error);
  white-space: pre;
}

.errorContainer {
  font-family: var(--app-text-main-font);
  font-size: 12px;
  color: var(--app-color-red-error);
  white-space: pre;
  word-break: break-all;
}

.renderTitle {
  align-self: flex-start;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  max-height: 30px;
}

.tooltipContainer {
  position: relative;
  flex: 0;
}

.icon {
  cursor: pointer;
  margin: 5px 0 0 20px;
  width: 18px;
  height: 18px;
  stroke: var(--app-color-gray-light);
  color: var(--app-color-gray-light);
  fill: var(--app-color-gray-light);
  opacity: 0.6;

  &:focus,
  &:focus-within {
    outline: none;
  }
}

.tooltip {
  opacity: 1 !important;
  box-shadow: 2px 2px 2px 2px #00000017 !important;
  border-radius: 8px !important;
  max-width: 180px !important;
  word-wrap: break-word;
  hyphens: auto;
  text-align: left;
  text-justify: inter-word;
  font-weight: 600;
  outline: none;
}

.renderDoubleSelect {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 5px;
}

.firstSelectContainer {
  display: flex;
  flex: auto;
  flex-direction: row;
  justify-content: flex-start;
}

.secondSelectContainer {
  display: flex;
  flex: auto;
  flex-direction: row;
  justify-content: flex-end;
}

.select {
  border: none;
  max-width: 110px;
}

.selectMenu {
  max-height: 200px;
  overflow-y: auto;
  width: auto;
}

.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.flex-centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-around {
  justify-content: space-around;
  align-items: center;
}

.flex-between {
  justify-content: space-between;
  align-items: center;
}

/* FieldWrapper */
.fieldWrapperActive {
  min-width: 260px;
  flex: 1;
  width: 100%;
  padding: 0 2.5% 0 5px;
  margin: 0px;
  border: none;
  border-radius: 4px;

  &[app-field-wrapper-variant="ungrouped"] {
    margin: 20px 0;
  }

  &[app-field-wrapper-active="active"] {
    border: 1px solid var(--app-color-gray-lighter);
    padding: 10px;
  }

  &[app-variant-has-error="true"] {
    border: 1px solid var(--app-color-red-error);
    padding: 10px;
  }

  .titleContainer {
    display: flex;
    flex: 1;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
  }

  .checkMarkContainer {
    //  margin: 8px 8px 0 0;
    display: flex;
    width: 32px;
    height: 32px;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;

    svg {
      width: 32px;
      height: 32px;
    }
  }

  .nonEditChildrenContainer {
    width: 100%;
    display: flex;
    flex: 1;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
  }

  .editParagraphContainer {
    position: relative;
    display: flex;
    flex: 1;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: stretch;
    width: 100%;
    /* backgroundColor: #FFFFF;  */
    border-radius: 8px;

    p {
      box-sizing: border-box;
      width: 100%;
      border: none;
      border-radius: 8px;
      font-family: var(--app-text-main-font);
      font-size: 30px;
      font-weight: 500;
      position: relative;
      padding: 0px 10px 0px 0px;
    }
  }
}

.fieldWrapperInactive {
  width: 100%;
  min-width: 260px;
  margin: 0px;
  padding: 0px;
  border: none;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;
  cursor: pointer;

  &[app-variant-inactive-ungrouped="true"] {
    margin: 20px 0;
  }

  &[app-variant-inactive-default-highlighted="true"] {
    padding: 10px;
    border: 1px solid var(--app-color-gray-lighter);
  }

  &[app-variant-inactive-with-error="true"] {
    border: 1px solid var(--app-color-red-error);
  }

  &[app-variant-inactive-read-only="true"] {
    cursor: text;
  }
}

.fieldWrapperInactiveImageContainer {
  display: flex;
  flex: 1;
  justify-content: space-around;
  padding: 20px;
}

.fieldWrapperInactiveImage {
  margin-right: auto;
  width: 18px;
  height: 18px;
}

.fieldWrapperInactiveSVG {
  width: 10px;
  height: 11px;
}

.fieldWrapperInactiveSpinnerContainer {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.fieldWrapperInactiveText {
  font-family: var(--app-text-main-font);
  font-size: 17px;
  color: var(--app-color-gray-dark);

  &[app-variant-palceholder="true"] {
    color: var(--app-color-gray-dark);
  }
}

