.renderBooleansOptions {
  align-self: flex-start;
  display: flex;
  flex-direction: row;
  align-items: center;

  p {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    color: var(--app-color-white-lightest);
  }

  >div {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    text-align: center;
    width: 115px;
    height: 40px;
    border-radius: 20px;
    margin: 4px;
    background-color: var(--app-color-gray-unselected);

    &[cont-variant="selected"] {
      background-color: var(--app-color-gray-selected);
    }
  }

  svg {
    margin: 2px 5px 0px 5px;
    width: 15.38px;
    height: 15.38px;
    filter: invert(0%) sepia(100%) saturate(0%) hue-rotate(50deg) brightness(300%) contrast(150%);
  }
}

.renderFieldOfComponent {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-sizing: border-box;
  border: 1px solid #d2d2d2;
  border-radius: 4px;
  margin: 2px 5px 0 5px;

  >div:first-child {
    width: 40px;
    height: 40px;
    margin: 0 0 0 5px;
    background-color: var(--app-color-white-dark);
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    img {
      margin: 2px;
      filter: invert(0%) sepia(0%) saturate(5000%) hue-rotate(50deg) brightness(100%) contrast(50%);

      &[img-visibility="visible"] {
        // background-color: var(--app-color-error);
        filter: invert(30%) sepia(10%) saturate(0%) hue-rotate(50deg) brightness(30%) contrast(80%);
      }
    }
  }

  p {
    font-family: var(--app-text-main-font);
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;

    &[p-type="mandatory"] {
      font-weight: 500;
      color: var(--app-color-gray-light);
    }

    &[p-mandatory="active"] {
      color: var(--app-color-gray-dark);
    }

    &[p-visibility="visible"] {
      color: var(--app-color-gray-dark);
    }

    &[p-visibility="notvisible"] {
      color: var(--app-color-gray-light);
    }
  }

  .optionsContainer {
    display: flex;
    justify-content: center;
    align-items: center;

    .mandatoryContainer {
      margin: 0 20px 0 5px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      svg {
        align-self: center;
        margin: 1px 5px 0 0;
        max-width: 16px;
        max-height: 16px;
        border-radius: 16px;
        filter: invert(30%) sepia(10%) saturate(0%) hue-rotate(50deg) brightness(70%) contrast(80%);
      }

      .notMandatory {
        margin: 1px 5px 0 0;
        width: 16px;
        height: 16px;
        border-radius: 16px;
        border: 1px solid #d2d2d2;
      }
    }

    .visibilityContainer {
      margin: 0 20px 0 5px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      svg {
        margin: 1px 5px 0 0;
        width: 16px;
        height: 16px;
      }
    }
  }
}

