.firstDiv_VII {
    position: relative;
    display: flex;
    height: 96%;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    margin: 2% 5% 2% 5%;
}
.logoImg_VII {
    position: absolute;
    left: -60px;
    top: 6px;
    right: 0;
    bottom: 0;
    width: 40px;
    height: 40px;
}
.headerContainer_VII {
    margin: 20px 0 0px 0;
    align-self: stretch;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    align-items: stretch;
    img {
        align-self: center;
        height: 30px;
        width: 32px;
    }
}
.header_VII {
    color: var(--app-color-gray-dark);
    font-family: var(--app-text-main-font);
    font-size: 2em;
    //  line-height: 2em;
    margin: 0;
    padding: 0 0 0 10px;
    text-align: left;
}
.subheadingContainer_VII {
    align-self: stretch;
    font-family: var(--app-text-main-font);
    display: flex;
    flex-direction: row;
    width: 100%;
    padding-top: 16px;
    padding-left: 10px;
    justify-content: center;
    align-items: center;

    .padH50 {
        padding: 0 50px 0 50px !important;
    }
    .centered {
        text-align: center;
        justify-content: center;
        align-items: center;
    }

    p {
        margin: 0px 0 16px 0;
        color: var(--app-color-gray-dark);
        font-family: var(--app-text-main-font);
        font-size: 1rem;
        line-height: 1.3rem;
        text-align: left;
        padding: 0 0 0 0;
    }
}
.subheading_VII {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: var(--app-color-gray-light);
    padding-bottom: 15px;
}
.justifiedLeft_VII {
    text-align: left;
    justify-content: flex-start;
    align-items: center;
}
.departmentsContainer_VII {
    display: flex;
    flex: 1;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    flex-wrap: wrap;
}

.closeContainer_VII {
    position: absolute;
    top: -19px;
    right: 19px;
}
.closeElement_VII {
    cursor: pointer;
    background-color: transparent;
    border: 0;
}
.closeIcon_VII {
    width: 48px;
    height: 48px;
}

.tutorialWrapper_VII {
    position: absolute;
    top: 1.5em;
    right: -68.5px;
    height: 62px;
    width: 137px;
    border-radius: 8px;
    background-color: #ffffff;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.1);
}
.tutorialContainer_VII {
    margin: 12px;
    cursor: pointer;
    padding: 0 10px 0 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.tutorialImg_VII {
    width: 11.58px;
    height: 15.98px;
}
.tutorialTextContainer_VII {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.tutorialText_VII {
    margin: 0 0 0 0;
    height: 24px;
    width: 61px;
    color: var(--app-color-gray-dark);
    font-family: var(--app-text-main-font);
    font-size: 1rem;
    line-height: 1.3rem;
    text-align: center;
}
.tutorialDuration_VII {
    margin: 0 0 0 0;
    height: 24px;
    width: 61px;
    color: var(--app-color-gray-dark);
    font-family: var(--app-text-main-font);
