    width: 100%;
    justify-content: space-between;
    align-items: center;
}
.backToPreviousButtonContainer_VII {
    cursor: pointer;
    align-self: center;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    img {
        opacity: 0.5;
        color: var(--app-color-gray-light);
        width: 22px;
        height: 22px;
        margin-right: 10px;
    }
    p {
        min-width: 135px;
        color: var(--app-color-gray-light);
        font-family: var(--app-text-main-font);
        font-size: 16px;
        font-weight: bold;
        letter-spacing: 0;
